___CSS_LOADER_EXPORT___.push([module.id, `.moonstone-primaryNavItem {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 2.5rem;
  margin: 0.5rem;
  color: var(--color-light);
  border-radius: 2rem;
  cursor: pointer;
  transition: color 0.2s, background-color 0.2s;
}
.moonstone-primaryNavItem.moonstone-selected {
  color: var(--color-light);
  background-color: var(--color-accent);
  cursor: default;
}
.moonstone-primaryNavItem:not(.moonstone-selected):hover {
  background-color: var(--color-gray_dark60);
}

.moonstone-primaryNavItem_content {
  display: flex;
  flex: 1;
  justify-content: flex-start;
  overflow: hidden;
  min-width: 2.5rem;
  max-width: 100%;
}
.moonstone-primaryNavItem_content:hover {
  color: var(--color-accent);
}

.moonstone-selected .moonstone-primaryNavItem_content:hover {
  color: currentColor;
}

.moonstone-primaryNavItem_iconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  min-width: 2.5rem;
  height: 2.5rem;
}

.moonstone-primaryNavItem_label {
  color: inherit;
  text-decoration: none;
}
.moonstone-primaryNavItem_label.moonstone-subtitle {
  color: var(--color-light60);
}

.moonstone-primaryNavItem_badge {
  position: absolute;
  top: calc(var(--spacing-nano) * -1);
  right: calc(var(--spacing-nano) * -1);
}

.moonstone-primaryNavItem_textContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  margin-left: var(--spacing-nano);
}

.moonstone-primaryNavItem_buttonContainer {
  display: flex;
  flex-direction: column;
  justify-content: right;
  overflow: hidden;
  margin: 0 var(--spacing-nano);
}

.moonstone-primaryNavItem_linkItem {
  width: 100%;
  margin: 0;
