___CSS_LOADER_EXPORT___.push([module.id, `.moonstone-valueList {
  flex-grow: 1;
  height: 300px;
  overflow-y: auto;
}

.moonstone-iconContainer {
  min-width: var(--spacing-large);
}
.moonstone-iconContainer .moonstone-displayNone {
  display: none;
}
.moonstone-iconContainer .moonstone-dragHandle {
  cursor: move;
}

.moonstone-valueListItem {
  align-items: center;
  width: calc(100% - 2px);
  margin: 1px;
  padding: var(--spacing-small);
  cursor: pointer;
}
.moonstone-valueListItem.selected {
  color: var(--color-white);
  background-color: var(--color-accent);
}
.moonstone-valueListItem:hover {
  color: var(--color-accent);
  background-color: var(--color-gray_light60);
}
.moonstone-valueListItem:hover > div > div > .moonstone-displayNone {
  display: block;
}

.moonstone-wrapper {
  flex-grow: 1;
  gap: var(--spacing-small);
}
.moonstone-wrapper .moonstone-listHolder {
  flex-grow: 1;
  gap: var(--spacing-small);
  min-width: 200px;
  padding: var(--spacing-small);
  border: 1px solid var(--color-gray40);
}
.moonstone-wrapper .moonstone-dragging {
  background-color: var(--color-light);
  box-shadow: 0 4px 8px rgba(19, 28, 33, 0.2);
}
.moonstone-wrapper .moonstone-dragging > div > div > .moonstone-displayNone {
  display: none;
}
.moonstone-wrapper .moonstone-noHoveEffect:hover {
  color: inherit;
  background-color: inherit;
}
.moonstone-wrapper .moonstone-noHoveEffect:hover > div > div > .moonstone-displayNone {
  display: none;
}
.moonstone-wrapper .moonstone-noOpacity {
  opacity: 0;
