___CSS_LOADER_EXPORT___.push([module.id, `.moonstone-secondaryNav {
  background: var(--color-gray_light40);
}
.moonstone-secondaryNav.moonstone-reversed {
  color: var(--color-light);
  background: var(--color-gray_dark);
}

.moonstone-secondaryNav_wrapper {
  position: relative;
  transition: max-width 0.4s ease-out;
}

.moonstone-secondaryNav_buttonToggle {
  position: absolute;
  top: var(--spacing-small);
  right: var(--spacing-small);
  z-index: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  color: var(--color-gray_dark);
  border: 0;
  border-radius: 24px;
  outline: none;
  background: none;
  cursor: pointer;
}
.moonstone-secondaryNav_buttonToggle:hover {
  background-color: var(--color-gray_light60);
}

.moonstone-secondaryNav_buttonToggle_reversed {
  color: var(--color-light);
}
.moonstone-secondaryNav_buttonToggle_reversed:hover {
  background-color: var(--color-accent);
}

.moonstone-secondaryNav_hidden {
  overflow: hidden;
  transition: width 0.2s ease-out;
}
.moonstone-secondaryNav_hidden .moonstone-secondaryNav_wrapper {
  max-width: 0;
  pointer-events: none;
}
.moonstone-secondaryNav_hidden .moonstone-secondaryNav_buttonToggle {
  position: fixed;
  left: 44px;
  background-color: var(--color-dark);
}
.moonstone-secondaryNav_hidden .moonstone-secondaryNav_buttonToggle:hover {
  background-color: var(--color-accent);
}`, "",{"version":3,"sources":["webpack://./node_modules/@jahia/moonstone/dist/components/SecondaryNav/SecondaryNav.css"],"names":[],"mappings":"AAAA;EACE,qCAAqC;AACvC;AACA;EACE,yBAAyB;EACzB,kCAAkC;AACpC;;AAEA;EACE,kBAAkB;EAClB,mCAAmC;AACrC;;AAEA;EACE,kBAAkB;EAClB,yBAAyB;EACzB,2BAA2B;EAC3B,UAAU;EACV,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,UAAU;EACV,6BAA6B;EAC7B,SAAS;EACT,mBAAmB;EACnB,aAAa;EACb,gBAAgB;EAChB,eAAe;AACjB;AACA;EACE,2CAA2C;AAC7C;;AAEA;EACE,yBAAyB;AAC3B;AACA;EACE,qCAAqC;AACvC;;AAEA;EACE,gBAAgB;EAChB,+BAA+B;AACjC;AACA;EACE,YAAY;EACZ,oBAAoB;AACtB;AACA;EACE,eAAe;EACf,UAAU;EACV,mCAAmC;AACrC;AACA;EACE,qCAAqC;AACvC","sourcesContent":[".moonstone-secondaryNav {\n  background: var(--color-gray_light40);\n}\n.moonstone-secondaryNav.moonstone-reversed {\n  color: var(--color-light);\n  background: var(--color-gray_dark);\n}\n\n.moonstone-secondaryNav_wrapper {\n  position: relative;\n  transition: max-width 0.4s ease-out;\n}\n\n.moonstone-secondaryNav_buttonToggle {\n  position: absolute;\n  top: var(--spacing-small);\n  right: var(--spacing-small);\n  z-index: 1;\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  width: 24px;\n  height: 24px;\n  padding: 0;\n  color: var(--color-gray_dark);\n  border: 0;\n  border-radius: 24px;\n  outline: none;\n  background: none;\n  cursor: pointer;\n}\n.moonstone-secondaryNav_buttonToggle:hover {\n  background-color: var(--color-gray_light60);\n}\n\n.moonstone-secondaryNav_buttonToggle_reversed {\n  color: var(--color-light);\n}\n.moonstone-secondaryNav_buttonToggle_reversed:hover {\n  background-color: var(--color-accent);\n}\n\n.moonstone-secondaryNav_hidden {\n  overflow: hidden;\n  transition: width 0.2s ease-out;\n}\n.moonstone-secondaryNav_hidden .moonstone-secondaryNav_wrapper {\n  max-width: 0;\n  pointer-events: none;\n}\n.moonstone-secondaryNav_hidden .moonstone-secondaryNav_buttonToggle {\n  position: fixed;\n  left: 44px;\n  background-color: