  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  list-style: none;
}
.moonstone-listItem .moonstone-listItem-image_small {
  flex: 0 0 56px;
  height: 56px;
  margin-right: var(--spacing-small);
}
.moonstone-listItem .moonstone-listItem-image_small img {
  max-width: 56px;
  max-height: 100%;
  margin: auto;
}
.moonstone-listItem .moonstone-listItem-image_big {
  flex: 0 0 96px;
  height: 96px;
  margin-right: var(--spacing-small);
}
.moonstone-listItem .moonstone-listItem-image_big img {
  max-width: 96px;
  max-height: 100%;
  margin: auto;
}

.moonstone-listItem_iconStart,
.moonstone-listItem_iconEnd {
  display: flex;
  align-items: center;
  width: 16px;
  height: 16px;
}

.moonstone-listItem_iconStart {
  margin-right: var(--spacing-nano);
}

.moonstone-listItem_iconEnd {
  margin-left: var(--spacing-nano);
}

.moonstone-listItem_description {
  display: -webkit-box;
  max-width: inherit;
  color: var(--color-gray60);
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
