  font-size: 0.875rem;
  line-height: 1.1875rem;
}

.moonstone-baseInput-element {
  font-size: 0.75rem;
  line-height: 1rem;
}

.moonstone-baseInput {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 100%;
  min-width: 40px;
  min-height: 24px;
  color: var(--color-gray_dark);
  cursor: text;
}
.moonstone-baseInput.moonstone-big {
  min-height: 32px;
}
.moonstone-baseInput.moonstone-outlined {
  border-radius: 2px;
  box-shadow: inset 0 0 0 1px var(--color-gray40);
}

.moonstone-baseInput_icon {
  height: 0.01em;
  max-height: 2em;
  margin-left: var(--spacing-small);
  color: var(--color-gray_dark);
  white-space: nowrap;
  pointer-events: none;
}

.moonstone-baseInput_elementsWrapper {
  gap: var(--spacing-nano);
  margin: 0 var(--spacing-nano);
}

.moonstone-baseInput-element {
  display: block;
  flex: 1;
  width: 0;
  min-width: 50px;
  height: 22px;
  margin: 0;
  padding: 0 var(--spacing-small);
  border: 0;
  -webkit-tap-highlight-color: transparent;
}
.moonstone-reversed .moonstone-baseInput-element {
  background-color: transparent;
}
.moonstone-baseInput-element.moonstone-big {
  height: 30px;
}
.moonstone-baseInput-element:invalid {
  box-shadow: none;
}
.moonstone-baseInput-element::-moz-placeholder {
  font-style: italic;
  color: var(--color-gray_dark60);
  opacity: 1;
}
.moonstone-baseInput-element:-ms-input-placeholder {
  font-style: italic;
  color: var(--color-gray_dark60);
  opacity: 1;
}
.moonstone-baseInput-element::placeholder {
  font-style: italic;
  color: var(--color-gray_dark60);
  opacity: 1;
}
.moonstone-baseInput-element:focus {
  outline: none;
}
.moonstone-baseInput-element:focus ~ .moonstone-baseInput_icon {
  color: var(--color-accent);
}
.moonstone-baseInput-element:-moz-read-only {
  box-shadow: none;
}
.moonstone-baseInput-element:read-only {
  box-shadow: none;
}
.moonstone-baseInput-element:disabled {
  color: var(--color-gray);
  background-color: var(--color-gray_light40);
  box-shadow: none;
  cursor: default;
  opacity: 1;
}
.moonstone-baseInput-element:disabled ~ .moonstone-baseInput_icon {
  color: var(--color-gray);
}
.moonstone-baseInput-element:-moz-placeholder-shown ~ .moonstone-baseInput_icon {
  color: var(--color-gray60);
}
.moonstone-baseInput-element:-ms-input-placeholder ~ .moonstone-baseInput_icon {
  color: var(--color-gray60);
}
.moonstone-baseInput-element:placeholder-shown ~ .moonstone-baseInput_icon {
  color: var(--color-gray60);
}

.moonstone-reversed[\\:not-has\\(.moonstone-menu\\)] .moonstone-baseInput_icon,
.moonstone-reversed[\\:not-has\\(.moonstone-menu\\)] .moonstone-baseInput {
  color: var(--color-white);
}

.moonstone-reversed:not(:has(.moonstone-menu)) .moonstone-baseInput_icon,
.moonstone-reversed:not(:has(.moonstone-menu)) .moonstone-baseInput {
  color: var(--color-white);
}

.moonstone-baseInput_clearButton {
  color: var(--color-gray);
  white-space: nowrap;
  cursor: pointer;
}

.moonstone-searchContextInput_element {
  min-width: 100px;
  max-width: 100px;
  background-color: var(--color-gray_light40);
