  margin-top: var(--spacing-medium);
}

.moonstone-radio-container[aria-disabled=true],
.moonstone-radio-container[aria-readonly=true] {
  cursor: default;
  opacity: 0.4;
  pointer-events: none;
}

.moonstone-radio-label {
  margin-left: var(--spacing-small);
  color: var(--color-gray-dark);
}

.moonstone-radio-description {
  margin-left: var(--spacing-large);
  color: var(--color-gray60);
}

.moonstone-radio {
  position: relative;
  width: 16px;
  height: 16px;
}

.moonstone-radio_icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: var(--color-gray);
  stroke: none;
  transform: scale(0) translateZ(0);
  -webkit-animation: radioBounce 0.1s linear forwards 0.05s;
          animation: radioBounce 0.1s linear forwards 0.05s;
  pointer-events: none;
  stroke-width: 0;
}

.moonstone-radio_input {
  position: relative;
  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}
.moonstone-radio_input:checked ~ .moonstone-radio_iconUnchecked, .moonstone-radio_input:not(:checked) ~ .moonstone-radio_iconChecked {
  visibility: hidden;
}
.moonstone-radio_input:checked ~ .moonstone-radio_iconChecked, .moonstone-radio_input:not(:checked) ~ .moonstone-radio_iconUnchecked {
  visibility: visible;
}
.moonstone-radio_input:checked ~ .moonstone-radio_icon {
  fill: var(--color-accent);
}
.moonstone-radio_input:hover ~ .moonstone-radio_icon {
  fill: var(--color-accent_dark);
}
.moonstone-radio_input:focus ~ .moonstone-radio_icon {
  fill: var(--color-accent_dark_contrast);
}

