  min-height: 64px;
  padding: var(--spacing-medium) var(--spacing-large) 0 var(--spacing-large);
  background-color: var(--color-white);
  box-shadow: 0 1px 8px var(--color-gray60);
}

.moonstone-header_actions > * + * {
  margin-left: var(--spacing-small);
}

.moonstone-header_main {
  margin-bottom: var(--spacing-small);
}

.moonstone-header_back {
  margin-right: var(--spacing-small);
}

.moonstone-header_title {
  margin-right: var(--spacing-big);
  color: var(--color-gray_dark);
}

.moonstone-header_search {
  margin-right: var(--spacing-large);
}

.moonstone-header_information {
  padding: var(--spacing-small) 0;
}
.moonstone-header_information:last-child {
  margin-bottom: var(--spacing-medium);
}

.moonstone-header_informationLeft,
.moonstone-header_informationRight {
  overflow: hidden;
}

.moonstone-header_informationLeft {
  margin-right: var(--spacing-big);
}

.moonstone-header_toolbar {
  height: 35px;
