___CSS_LOADER_EXPORT___.push([module.id, `.moonstone-dropdown_container {
  position: relative;
}

.moonstone-dropdown_icon {
  display: flex;
  flex: 0 0 auto;
  margin-right: var(--spacing-nano);
}

.moonstone-dropdown_chevronDown {
  flex: 0 0 auto;
}

.moonstone-dropdown_tags {
  gap: var(--spacing-nano) var(--spacing-small);
}

.moonstone-disabled > .moonstone-treeView_item {
  color: var(--color-gray40);
  cursor: not-allowed;
  pointer-events: none;
}
.moonstone-disabled > .moonstone-treeView_item:hover {
  pointer-events: none;
}

.moonstone-dropdown {
  padding: var(--spacing-nano) var(--spacing-small);
  border-radius: 2px;
  cursor: pointer;
}
.moonstone-dropdown.moonstone-disabled {
  color: var(--color-gray40);
  cursor: not-allowed;
  pointer-events: none;
}
.moonstone-dropdown.moonstone-disabled:hover {
  pointer-events: none;
}
.moonstone-dropdown.moonstone-medium {
  min-height: 32px;
}
.moonstone-dropdown.moonstone-opened {
  box-shadow: inset 0 0 0 1px var(--color-accent);
}
.moonstone-dropdown:hover {
  color: var(--color-accent);
}
.moonstone-dropdown:focus {
  outline: none;
  box-shadow: inset 0 0 0 1px var(--color-accent);
}
.moonstone-dropdown:not(.moonstone-filled) .moonstone-dropdown_label {
  font-style: italic;
  color: var(--color-gray_dark60);
}

.moonstone-dropdown_outlined {
  color: var(--color-gray_dark);
  box-shadow: inset 0 0 0 1px var(--color-gray40);
}
.moonstone-dropdown_outlined.moonstone-disabled {
  color: var(--color-gray);
  background-color: var(--color-gray_light40);
  box-shadow: inset 0 0 0 1px var(--color-gray40);
