___CSS_LOADER_EXPORT___.push([module.id, `.moonstone-accordionItem {
  min-height: 48px;
  border-top: 1px solid var(--color-gray_light);
  border-bottom: 1px solid var(--color-gray_light);
  overflow: hidden;
}
.moonstone-accordionItem:only-child {
  border-bottom: none;
}
.moonstone-accordionItem + .moonstone-accordionItem {
  border-top: 0;
}
.moonstone-accordionItem.moonstone-reversed {
  border-color: var(--color-gray);
}

.moonstone-accordionItem_header {
  width: 100%;
  height: 48px;
  padding: 0 var(--spacing-medium);
  background-color: var(--color-gray_light60);
  cursor: pointer;
}
.moonstone-accordionItem_header.moonstone-selected {
  border-bottom: 1px solid var(--color-gray_light);
  cursor: default;
}
.moonstone-accordionItem_header.moonstone-selected:focus {
  color: var(--color-light);
  background-color: var(--color-dark60);
}
.moonstone-accordionItem_header:active {
  background-color: var(--color-gray40);
}
.moonstone-accordionItem_header:focus, .moonstone-accordionItem_header:not(.moonstone-selected):not(:active):hover {
  color: var(--color-dark);
  background-color: var(--color-gray_light);
}
.moonstone-accordionItem_header.moonstone-reversed {
  color: var(--color-light);
  background-color: var(--color-dark60);
}
.moonstone-accordionItem_header.moonstone-reversed.moonstone-selected {
  color: var(--color-light);
  border-color: var(--color-gray);
  background-color: var(--color-dark60);
}
.moonstone-accordionItem_header.moonstone-reversed:active {
  background-color: var(--color-black);
}
.moonstone-accordionItem_header.moonstone-reversed:focus, .moonstone-accordionItem_header.moonstone-reversed:not(.moonstone-selected):not(:active):hover {
  color: var(--color-light);
  background-color: var(--color-gray40);
}

.moonstone-accordionItem_iconContainer {
  margin-right: var(--spacing-small);
}

.moonstone-accordionItem_content {
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
