___CSS_LOADER_EXPORT___.push([module.id, `[role=treeitem] {
  --treeItem-indent: calc((var(--spacing-medium) + var(--spacing-nano)) * var(--treeItem-depth, 0) + var(--spacing-medium));
  --treeItem-color: inherit;
  --treeItem-color_hover: var(--color-dark);
  --treeItem-color_selected: var(--color-light);
  --treeItem-color_selected_hover: var(--color-light);
  --treeItem-background: inherit;
  --treeItem-background_hover: var(--color-gray_light40);
  --treeItem-background_selected: var(--color-accent);
  --treeItem-background_selected_hover: var(--color-accent);
}

.moonstone-treeView_item {
  flex-wrap: nowrap;
  height: 32px;
  padding-right: var(--spacing-small);
  padding-left: var(--treeItem-indent);
  color: var(--treeItem-color);
  background-color: var(--treeItem-background);
  cursor: pointer;
}
.moonstone-treeView_item:hover {
  color: var(--treeItem-color_hover);
  background-color: var(--treeItem-background_hover);
}
.moonstone-treeView_item.moonstone-small {
  height: 24px;
}
.moonstone-treeView_item.moonstone-selected {
  color: var(--treeItem-color_selected);
  background-color: var(--treeItem-background_selected);
}
.moonstone-treeView_item.moonstone-selected:hover {
  color: var(--treeItem-color_selected_hover);
  background-color: var(--treeItem-background_selected_hover);
}
.moonstone-treeView_item.moonstone-reversed {
  --treeItem-color: var(--color-gray_light);
  --treeItem-color_hover: var(--color-light);
  --treeItem-background_hover: var(--color-gray40);
}
.moonstone-treeView_item.moonstone-disabled {
  background: none;
  cursor: not-allowed;
}

.moonstone-treeView_itemIconStart,
.moonstone-treeView_itemToggle {
  margin-right: var(--spacing-nano);
}

.moonstone-treeView_itemIconStart {
  min-width: 12px;
  min-height: 12px;
}

.moonstone-treeView_itemIconEnd {
  margin-left: var(--spacing-nano);
}

.moonstone-treeView_itemToggle {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.moonstone-treeView_itemLabel {
  align-self: stretch;
  min-width: 0;
}
.moonstone-treeView_item.moonstone-disabled .moonstone-treeView_itemLabel {
  color: var(--color-gray40);
}`, "",{"version":3,"sources":["webpack://./node_modules/@jahia/moonstone/dist/components/TreeView/TreeView.css"],"names":[],"mappings":"AAAA;EACE,yHAAyH;EACzH,yBAAyB;EACzB,yCAAyC;EACzC,6CAA6C;EAC7C,mDAAmD;EACnD,8BAA8B;EAC9B,sDAAsD;EACtD,mDAAmD;EACnD,yDAAyD;AAC3D;;AAEA;EACE,iBAAiB;EACjB,YAAY;EACZ,mCAAmC;EACnC,oCAAoC;EACpC,4BAA4B;EAC5B,4CAA4C;EAC5C,eAAe;AACjB;AACA;EACE,kCAAkC;EAClC,kDAAkD;AACpD;AACA;EACE,YAAY;AACd;AACA;EACE,qCAAqC;EACrC,qDAAqD;AACvD;AACA;EACE,2CAA2C;EAC3C,2DAA2D;AAC7D;AACA;EACE,yCAAyC;EACzC,0CAA0C;EAC1C,gDAAgD;AAClD;AACA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;;EAEE,iCAAiC;AACnC;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;AACA;EACE,0BAA0B;AAC5B","sourcesContent":["[role=treeitem] {\n  --treeItem-indent: calc((var(--spacing-medium) + var(--spacing-nano)) * var(--treeItem-depth, 0) + var(--spacing-medium));\n  --treeItem-color: inherit;\n  --treeItem-color_hover: var(--color-dark);\n  --treeItem-color_selected: var(--color-light);\n  --treeItem-color_selected_hover: var(--color-light);\n  --treeItem-background: inherit;\n  --treeItem-background_hover: var(--color-gray_light40);\n  --treeItem-background_selected: var(--color-accent);\n  --treeItem-background_selected_hover: var(--color-accent);\n}\n\n.moonstone-treeView_item {\n  flex-wrap: nowrap;\n  height: 32px;\n  padding-right: var(--spacing-small);\n  padding-left: var(--treeItem-indent);\n  color: var(--treeItem-color);\n  background-color: var(--treeItem-background);\n  cursor: pointer;\n}\n.moonstone-treeView_item:hover {\n  color: var(--treeItem-color_hover);\n  background-color: var(--treeItem-background_hover);\n}\n.moonstone-treeView_item.moonstone-small {\n  height: 24px;\n}\n.moonstone-treeView_item.moonstone-selected {\n  color: var(--treeItem-color_selected);\n  background-color: var(--treeItem-background_selected);\n}\n.moonstone-treeView_item.moonstone-selected:hover {\n  color: var(--treeItem-color